import "bootstrap";
import "materialize-css";
import "jquery";
//import Web3 from "web3";

import Vue from "vue";
import VueRouter from "vue-router";
Vue.use(VueRouter);

import GuildsScroller from "./vue/guilds-scroller/index.vue";
import GuildView from "./vue/guild-view/index.vue";
import MessagesView from "./vue/messages-view/index.vue";

import "./scss/style.scss";

import Utils from "./js/utils.js";
import Anchor from "./js/Anchor";
import Guild from "./js/Guild";

let router = new VueRouter({
	routes: [
		{
			path: "/guild/:guildId",
			component: GuildView,
			children: [
				{ path: "channel/:textChannelId", component: MessagesView }
			]
		}
	]
});

let vue = new Vue({
	router,
	data: {
		dataService: null,
		guilds: [],
		friendsOnline: 0,
	}
});

Vue.component("guilds-scroller", GuildsScroller);
Vue.component("guild-view", GuildView);
Vue.config.devtools = process.env.NODE_ENV === "development";

setInterval(() => {
	if (window.location.hash === "#/") {
		window.location.hash = "#/guild/0";
	}
}, 500);

function main() {
	Promise.all([
		import("ipfs"),
		import("orbit-db"),
		import("orbit-db-store")
	]).then(([ { default: IPFS }, { default: OrbitDB }, { default: Store } ]) => {
		console.log("Launching IPFS node...");
		//let ipfs = IpfsApi("localhost", "5001");
		let ipfs = new IPFS({
			EXPERIMENTAL: {
				pubsub: true
			}
		});

		ipfs.on("ready", async () => {
			console.log("Success!");

			let orbit = new OrbitDB(ipfs);

			let userData;
			
			if (Utils.storageAvailable("localStorage")) {

				let address = localStorage.getItem("anchor.userDatabaseAddress");
				console.log(address);
				if (address) {
					userData = await orbit.keyvalue(address);
				} else {
					userData = await orbit.keyvalue("anchorUserData", {write: [orbit.key.getPublic("hex")]});

					//await userData.set("id", Math.floor((Math.random() * 100000) + 1));
					await userData.set("name", "");
					await userData.set("avatar", "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRWB6W7GLsVLLDYp72NtIGnB4r1aJVpVnOed17IB2abKLY_8tAl#.png");
					await userData.set("guilds", []);

					localStorage.setItem("anchor.userDatabaseAddress", userData.address.toString());
				}
				console.log(userData);
				global.userData = userData;

				userData.events.on("replicated", () => {
					console.log("Replicated!");
				});

				//await userData.load();

				let AnchorAPI = new Anchor(ipfs, orbit, userData);
				let dataService = AnchorAPI.getData();

				vue.dataService = dataService;

				global.vue = vue;
				global.AnchorAPI = AnchorAPI;

				dataService.getGuilds((err, guilds) => {
					dataService.getFriends((err, friends) => {
						let data = {
							guilds,
							friendsOnline: friends.friendsOnline
						};
				
						console.log(data);

						Object.assign(vue, data);
				
						vue.$mount("#app");
					});
				});
			}
		});
	}).catch(console.error);
}

main();