import Channel from "./Channel";


export default class Guild {

	constructor(obj) {
		this.id = 0;
		this.name = "";
		this.img = "";
		this.channels = [];
		this.dataService = null;
		this.databaseAddr = null;
		this.database = null;
		this.anchorApi = null;
		if (obj) Object.assign(this, obj);
		if (this.databaseAddr) {
			this.database = this.anchorApi.orbitdb.keyvalue(this.databaseAddr);
			//this.database.load();

			//this.database.events.on("ready", () => {
			this.id = this.database.get("id");
			this.name = this.database.get("name");
			this.img = this.database.get("img");

			let channelData = this.database.get("channels");
			channelData.forEach(channelAddr => {
				this.channels.push(new Channel({
					dataService: this.dataService,
					anchorApi: this.anchorApi,
					databaseAddr: channelAddr
				}));
			});
			//});
		}
	}

	getChannels() {
		return this.channels;
	}

	getTextChannel(id, callback) {
		try {
			let channels = this.getChannels();
				
			let ret = null;
			for (let i in channels) {
				let el = channels[i];
				if (el.id == id) {
					ret = el;
					break;
				}
			}
			
			callback(null, ret);
		} catch(err) {
			callback(err, null);
		}
	}
}