import Guild from "./Guild";
import Friends from "./Friends";
import DataService from "./DataService";

export default class Anchor {

	constructor(ipfs, orbitdb, userData) {
		this.username = userData.get("username");
		this.ipfs = ipfs;
		this.userData = userData;
		this.orbitdb = orbitdb;
		this.dataService = new DataService(this);
	}

	getData() {
		return this.dataService;
	}

	async createGuild(name) {
		let database = await this.orbitdb.keyvalue(name.toLowerCase().replace(" ", "-"), {write: [this.orbitdb.key.getPublic("hex")]});

		await database.set("id", Math.floor((Math.random() * 100000) + 1));
		await database.set("name", name);
		await database.set("img", name);
		await database.set("channels", []);

		let guilds = this.userData.get("guilds") || [];
		guilds.push(database.address.toString());
		await this.userData.set("guilds", guilds);

		database.close();
	}

	async createChannel(guild, name) {
		let database = await this.orbitdb.keyvalue(guild.name.toLowerCase().replace(" ", "-")+"#"+name.toLowerCase().replace(" ", "-"), {write: [this.orbitdb.key.getPublic("hex")]});
		let feed = await this.orbitdb.feed(guild.name.toLowerCase().replace(" ", "-")+"#"+name.toLowerCase().replace(" ", "-")+"#feed", {write: ["*"]});

		await database.set("id", Math.floor((Math.random() * 100000) + 1));
		await database.set("name", name);

		await database.set("feed", feed.address.toString());

		let channels = guild.database.get("channels");
		channels.push(database.address.toString());
		await guild.database.set("channels", channels);

		feed.close();
		database.close();
	}
}