export default class MessageHistory {

	constructor(channel, dataService) {
		this.channel = channel;
		this.guild = this.channel.guild;
		this.dataService = dataService;
		this.feed = null;

		if (this.channel.feedAddr) {
			this.feed = this.dataService.anchorApi.orbitdb.feed(this.channel.feedAddr);
			this.feed.load();
		}
	}

	get(number, callback) {
		this.dataService.getMessages(this, number, callback);
	}
}