export default {
	checkConnection(callback) {
		if (!web3.isConnected()) {
			callback(false);
		} else {
			callback(true);
		}
	},

	web3Setup(callback) {
		if (typeof web3 !== "undefined") {
			web3 = new Web3(web3.currentProvider);
		} else {
			if (process.env.NODE_ENV === "development") {
				web3 = new Web3(new Web3.providers.HttpProvider("http://localhost:7777"));
			}
		}
		this.checkConnection(callback);
	},
	storageAvailable(type) {
		let storage;
		try {
			storage = window[type];
			let x = "__storage_test__";
			storage.setItem(x, x);
			storage.removeItem(x);
			return true;
		}
		catch(e) {
			return e instanceof DOMException && (
				// everything except Firefox
				e.code === 22 ||
				// Firefox
				e.code === 1014 ||
				// test name field too, because code might not be present
				// everything except Firefox
				e.name === "QuotaExceededError" ||
				// Firefox
				e.name === "NS_ERROR_DOM_QUOTA_REACHED") &&
				// acknowledge QuotaExceededError only if there's something already stored
				storage.length !== 0;
		}
	},
	showDialog(html, onYes, onNo, yesText, noText) {
		let body = $("body");
		let dialog = $("<div id=\"dialog\"></div>");
		let content = $("<p></p>").html(html);
		let fog = $("<div id=\"fog\"></div>");

		let yes = $("<button></button>");
		let no = $("<button></button>");

		if (onYes && !onNo) {
			yes.text(yesText || "Ok")
				.attr("id", "OK")
				.addClass("button waves-effect waves-light btn");

			yes.click(() => {
				dialog.remove();
				fog.remove();
				onYes();
			});

			dialog.append(yes);
		} else if (onYes && onNo) {

			yes.text(yesText || "Yes")
				.attr("id", "Yes")
				.addClass("button waves-effect waves-light btn");

			yes.click(() => {
				dialog.remove();
				fog.remove();
				onYes();
			});
		
			dialog.append(yes);

			no.text(noText || "No")
				.attr("id", "No")
				.addClass("button waves-effect waves-light btn");

			no.click(() => {
				dialog.remove();
				fog.remove();
				onNo();
			});

			dialog.append(no);
		}

		body.prepend(fog);
		dialog.prepend(content);
		body.append(dialog);
	}

};