import MessageHistory from "./MessageHistory";

export default class Channel {

	constructor(obj) {
		this.guild = null;
		this.id = 0;
		this.name = "";
		this.dataService = null;
		this.databaseAddr = null;
		this.database = null;
		this.anchorApi = null;
		this.feedAddr = null;
		if (obj) Object.assign(this, obj);
		if (this.databaseAddr) {
			this.database = this.anchorApi.orbitdb.keyvalue(this.databaseAddr);
			this.database.load();

			this.database.events.on("ready", () => {
				this.id = this.database.get("id");
				this.name = this.database.get("name");
	
				this.feedAddr = this.database.get("feed");
			});
		}
	}

	getMessages() {
		this.messageHistory = this.messageHistory || new MessageHistory(this, this.dataService);
		return this.messageHistory;
	}

	sendMsg(msg) {
		this.dataService.sendMsg(this.getMessages(), msg);
	}
}