import MockData from "./MockData";
import Friends from "./Friends";
import Message from "./Message";
import Guild from "./Guild";

const USE_MOCK_DATA = false;

export default class DataService {

	constructor(anchorApi) {
		this.USE_MOCK_DATA = USE_MOCK_DATA;
		this.anchorApi = anchorApi;
		this.cachedGuilds = {
			"0": new Guild({
				id: 0,
				name: "Private messages",
				img: "chat",
				anchorApi: this.anchorApi,
				dataService: this
			}),
		};
		if (USE_MOCK_DATA) {
			this.mockData = MockData(this);
		}
	}

	async getGuilds(callback) {
		if (USE_MOCK_DATA) {
			callback(null, this.mockData.guilds);
		} else {
			let data = this.anchorApi.userData.get("guilds");
			let arr = data || [];

			arr.unshift("0");

			let guilds = [];
			arr.forEach(guildAddr => {
				if (this.cachedGuilds[guildAddr]) {
					guilds.push(this.cachedGuilds[guildAddr]);
				} else {
					let guild = new Guild({
						anchorApi: this.anchorApi,
						databaseAddr: guildAddr,
						dataService: this
					});

					guilds.push(guild);
					this.cachedGuilds[guildAddr] = guild;
				}
			});

			callback(null, guilds);
		}
	}

	async getGuild(id, callback) {
		try {
			this.getGuilds((err, guilds) => {
				if (err) throw err;
				
				let ret = null;
				for (let i in guilds) {
					let el = guilds[i];
					if (el.id == id) {
						ret = el;
						break;
					}
				}
				callback(null, ret);
			});
		} catch(err) {
			callback(err, null);
		}
	}

	async getMessages(history, number, callback) {
		let messages;
		if (USE_MOCK_DATA) {
			messages = this.mockData.messages[history.channel.guild.id][history.channel.id];
		} else {
			messages = history.feed.iterator({ limit: number })
				.collect()
				.map((e) => e.payload.value);
		}
		if (typeof messages !== "undefined") {
			let startI = messages.length-number;
			if (startI > 0) messages = messages.slice(startI);

			callback(null, messages);
		} else {
			callback(null, []);
		}
	}

	async sendMsg(history, msg) {
		if (USE_MOCK_DATA) {
			this.mockData.messages[history.channel.guild.id][history.channel.id].push(new Message({
				author: this.anchorApi.username,
				text: msg
			}));
		} else {
			return await history.feed.add(new Message({
				author: this.anchorApi.userData.address.toString(),
				text: msg
			}));
		}
	}

	async getFriends(callback) {
		let f = new Friends();
		if (USE_MOCK_DATA) {
			Object.assign(f, this.mockData.friends);
			callback(null, f);
		} else {
			callback(null, f);
		}
	}

}